<template>
    <div class="main-page">
        <div class="headerTag">调拨单</div>
        <form class="h-form">
            <item name="单据编号">
                <input v-model="info.FNumber" />
            </item>
            <item must name="调拨日期">
                <input v-model="info.FDate" readonly @click="chooseTime('FDate')" />
            </item>
            <item must name="调出仓库">
                <input v-model="info.OutWarehouseName" readonly @click="chooseWarehouse(1)" />
            </item>
            <item must name="调入仓库">
                <input v-model="info.InWarehouseName" readonly @click="chooseWarehouse(3)" />
            </item>
            <item must name="调出操作人">
                <input v-model="info.OutOperatorName" readonly @click="choosePerson(1)" />
            </item>
            <item must name="调入操作人">
                <input v-model="info.InOperatorName" readonly @click="choosePerson(3)" />
            </item>

            <item name="说明">
                <textarea v-model="info.Content" />
            </item>
            <div style="display:flex;white-wrap:nowrap;align-items: center;justify-content: space-between;">
                <div class="headerTag">物料明细</div>
                <van-button size="small" style="margin-right:15px" :color="$config.theme" @click="chooseMaterial">新增</van-button>
            </div>
            <div class="car-item shadow" v-for="(item,index) in info.EntryList" :key="index">
                <div class="car-item-line" :style="{color:$config.theme,fontWeight:800}">物料：{{item.MaterialName}}</div>
                <!-- <div class="car-item-line" style="display:flex;white-wrap:nowrap;align-items: center;">批次：
                    <input style="width:calc(100% - 100px);height:26px;text-align:left;background:#f2f2f2" type="number" v-model="item.LotNumber" />
                </div> -->
                <div class="car-item-line" style="display:flex;white-wrap:nowrap;align-items: center;">数量：
                    <input style="width:calc(100% - 100px);height:26px;text-align:left;background:#f2f2f2" type="number" v-model="item.Qty" />
                </div>
            </div>
        </form>
        <van-button type="primary" block size="normal" :color="$config.theme" style="width: calc(100% - 30px); margin:3px 15px" @click="submit" :loading="loading">提交</van-button>
        <van-popup v-model="timeShow" position="bottom">
            <van-datetime-picker type="date" title="选择日期" @confirm="confirmTime" />
        </van-popup>

    </div>
</template>

<script>
export default {
    name: "MoveBillAE",
    props: {},
    components: {},
    data() {
        return {
            apis: {
                edit: this._api.OSMS_MoveBillEdit,
                create: this._api.OSMS_MoveBillCreate,
                list: this._api.OSMS_MoveBillManage,
                delete: this._api.OSMS_MoveBillDelete,
                audit: this._api.OSMS_MoveBillAudit,
                details: this._api.OSMS_MoveBillDetails
            },
            loading: false,
            curId: null,
            info: {
                PersonName: "",
                FName: "",
                EntryList: []
            },
            rules: {
                FDate: "请选择调拨日期！",
                OutWarehouseName: "请选调出仓库！",
                InWarehouseName: "请选调入仓库！",
                OutOperatorName: "请选调出操作人！",
                InOperatorName: "请选调入操作人！"
            },
            curTimeFieldName: "",
            timeShow: false,
            type: 1
        }
    },
    methods: {
        async submit() {
            await this.$u.checkForm(this.info, this.rules)
            this.loading = true
            let apiFun = this.curId ? this.apis.edit : this.apis.create
            apiFun(this.info).then(res => {
                if (res) {
                    this.$u.msg(this.info.Id ? "编辑成功！" : "创建成功！")
                    setTimeout(() => {
                        this.$router.back()
						
                    }, 200);
                }
                this.loading = false
            })
        },
        chooseTime(fieldName) {
            this.curTimeFieldName = fieldName
            this.timeShow = true
        },
        confirmTime(time) {
            this.timeShow = false
            this.info[this.curTimeFieldName] = this.$time(time).format("YYYY-MM-DD")
        },
        chooseWarehouse(type) {
            this.type = type
            window.temFun = (warehouse) => {
                if (this.type == 1) {
                    this.$set(this.info, "OutWarehouseName", warehouse.WarehouseName)
                    this.$set(this.info, "OutWarehouseId", warehouse.Id)
                } else if (this.type == 3) {
                    this.$set(this.info, "InWarehouseName", warehouse.WarehouseName)
                    this.$set(this.info, "InWarehouseId", warehouse.Id)
                }
            }
            this.$router.push(`/modular/oa/WarehouseChoose`)
        },
        choosePerson(type) {
            this.type = type
            window.temFun = (person) => {
                if (this.type == 1) {
                    this.$set(this.info, "OutOperatorName", person.Fname)
                    this.$set(this.info, "OutOperatorId", person.Id)
                } else if (this.type == 3) {
                    this.$set(this.info, "InOperatorName", person.Fname)
                    this.$set(this.info, "InOperatorId", person.Id)
                }
            }
            this.$router.push(`/modular/oa/ChoosePerson`)
        },
        chooseMaterial() {
            window.temFun = (material) => {
                this.info.EntryList.push({
                    LotNumber: null,
                    Qty: null,
                    MaterialName: material.FName,
                    MaterialId: material.Id
                })
            }
            this.$router.push(`/modular/oa/MaterialChoose`)
        }
    },
    mounted() {
        this.info = {
            EntryList: []
        }
        this.curId = this.$route.query.Id
    }
}
</script>

<style lang="scss" >
.car-item {
    width: calc(100% - 30px);
    margin-left: 15px;
    margin-bottom: 15px;
    padding: 10px 15px;
    font-size: 14px;

    .car-item-line {
        line-height: 30px;
        margin-right: 15px;
    }
}
</style>